.home-container {
    padding: 0px 0 200px 0;
    max-width: 1600px;
    margin: 0 auto;
}

.load-more {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin: 50px 0 0 0;
}

.photos-grid-view {
    margin: 0px auto;
}

@media (min-width: 800px) {
    .photos-grid-view>figure:hover {
        background: #eeeeee;
    }

    .photos-grid-view>figure {
        width: 300px;
        border-radius: 10px;
        max-width: 320px;
        padding: 7px;
        border: 5px solid #fff;
        margin-top: 15px;
        background: #ffffff;
        transition: .2s;
        cursor: pointer;
    }

    .owner-date {
        top: -5px;
    }
}

@media (max-width: 800px) {
    .photos-grid-view>figure {
        width: 450px;
        border-radius: 10px;
        max-width: 320px;
        padding: 0px;
        border: 5px solid #fff;
        margin-top: 15px;
        background: #ffffff;
    }

    .owner-date {
        top: -12px;
    }
}

@media (max-width: 400px) {
    .photos-grid-view>figure {
        width: 300px;
        border-radius: 10px;
        padding: 0px;
        border: 5px solid #fff;
        margin-top: 15px;
        background: #ffffff;
    }

    .owner-date {
        top: -12px;
    }
}

.owner {
    float: left;
    margin-left: 5px;
    color: #666666;
}

.owner:hover {
    color: #333333;
}

.owner-date {
    float: left;
    font-size: 12px;
    // margin-left: 5px;
    padding: 3px 2px;
    position: absolute;
    text-align: center;
    width: 70%;
    left: 50%;
    transform: translateX(-50%);
    // bottom: 38px;
    background: #fff;
    color: #000;
    opacity: 1;
    pointer-events: none;
    // mix-blend-mode: difference;
    border-radius: 10px;
    // opacity: .8;
}

@media (max-width: 500px) {
    .logo-text, .username {
        display: none;
    }
}

#ellel {
    width: 100% !important;
    max-width: 100% !important;
}




















// свой masonry

.masonry-with-columns {
    columns: 6 310px;
    column-gap: 1rem;

    div {
        width: 650px;
        background: #EC985A;
        color: white;
        margin: 0 1rem 1rem 0;
        display: inline-block;
        width: calc(100% + 250px);
        text-align: center;
        font-family: system-ui;
        font-weight: 900;
        font-size: 2rem;
    }

    @for $i from 1 through 36 {
        div:nth-child(#{$i}) {
            $h: (random(400) + 100) + px;
            height: $h;
            line-height: $h;
        }
    }
}
